import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { withStyles, Typography, Button } from '@material-ui/core';
import { withRouter } from 'react-router';
import styles from './styles';

const NotFound = memo(({ classes, history }) => (
  <div className={classes.root} style={{ marginTop: 100 }}>
    <p className={classes.center}>
      <img src={require('../../assets/images/logo.png')} width={150} alt="Lobby" className={classes.logo} />
    </p>
    <p className={classes.center}>
      <Typography variant="h6">
        <b>Ops...Parece que voc acessou uma pgina no encontrada</b>
      </Typography>
    </p>
    <p className={classes.center}>
      <Button
        variant="contained"
        color="primary"
        size="large"
        onClick={() => history.replace('/')}
      >
        Voltar para home page
      </Button>
    </p>
  </div>
));

NotFound.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(NotFound);
