import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { useMediaQuery, Typography, Button } from '@material-ui/core';
import { withStyles, useTheme } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import Session from '../../../utils/session';
import styles from './styles';

const TicketsWelcome = memo(({ classes, history }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const imgWidth = isSmallScreen ? 275 : 480;
  const imgHeight = isSmallScreen ? 76 : 132;
  return (
    <section className={classes.welcome}>
      <div className={classes.container}>
        <div className={classes.root}>
          <Typography variant="h5" className={classes.center}>
            <b>Organize suas demandas</b>
          </Typography>
          <p className={classes.center}>
            Organize de forma inteligente as demandas de seu ambiente de trabalho.
            <br />
            Todos colaboradores podem abrir solicitaes facilmente,
            seja no computador, celular, QR code ou Slack.
            <br />
            Chega de dor de cabea com planilhas ou sistemas que no te servem mais.
          </p>
          <p className={classes.center}>
            <img src={require('../../../assets/images/tickets-welcome.png')} width={imgWidth} height={imgHeight} alt="Organize suas demandas" />
          </p>
          <br />
          <p className={classes.center}>
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={() => {
                Session.setTicketsWelcomeVisited();
                history.replace('/tickets');
              }}
            >
              Comear
            </Button>
          </p>
        </div>
      </div>
    </section>
  );
});

TicketsWelcome.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(TicketsWelcome);
