/* global window */

import React, { memo } from 'react';
import { get, map, differenceBy, includes } from 'lodash';
import { compose, withProps, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import {
  DialogContent, Box, List, ListItem, ListItemSecondaryAction, IconButton
} from '@material-ui/core';
import ClearIcon from '@material-ui/icons/Clear';
import PropTypes from 'prop-types';

// Project modules
import config from 'config';
import MembersService from 'services/members';
import TicketsService from 'services/tickets';
import withViewerData from 'utils/with-viewer-data';
import withRequest from 'utils/with-request';
import withFormData from 'utils/with-form-data';
import { FormSelectField } from 'components/FormFields';
import Avatar from 'components/Avatar';
import styles from './styles';

const ParticipantsTab = ({
  classes, viewer, setFormData, formData: {
    validationErrors: errors, participantId
  },
  ticket: { participants }, members, loading,
  createParticipant, destroyParticipant
}) => (
  <DialogContent className={classes.participantContent}>
    {
      viewer.is_agent && (
        <FormSelectField
          label="Usurios"
          errors={errors.participants}
          disabled={loading}
          value={participantId}
          onChangeValue={(newVal) => {
            setFormData({ participantId: newVal });
            createParticipant();
          }}
          options={members.map((i) => ({ label: i.fullname, value: i.id }))}
          emptyOptionLabel="Adicione um participante"
        />
      )
    }
    <Box mt={1} mx={[0, '10%']} className={classes.limitedSizeScrollable}>
      <List>
        {
          map(participants, participant => (
            <ListItem key={participant.id}>
              <Avatar name={participant.fullname} photoUrl={participant.photoUrl} />
              {
                (viewer.is_agent || viewer.id === participant.id) && (
                  <ListItemSecondaryAction>
                    <IconButton
                      disabled={loading}
                      onClick={() => destroyParticipant(participant.id)}
                      className={classes.removeButton}
                    >
                      <ClearIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                )
              }
            </ListItem>
          ))
        }
      </List>
    </Box>
  </DialogContent>
);
ParticipantsTab.propTypes = {
  classes: PropTypes.object,
  viewer: PropTypes.object,
  setFormData: PropTypes.func,
  formData: PropTypes.shape({
    validationErrors: PropTypes.object,
    participantId: PropTypes.string
  }),
  ticket: PropTypes.shape({
    participants: PropTypes.array
  }),
  members: PropTypes.array,
  loading: PropTypes.bool,
  createParticipant: PropTypes.func,
  destroyParticipant: PropTypes.func
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members',
  props: ({ membersData, ownProps: { ticket: { participants } } }) => ({
    members: differenceBy(get(membersData, 'members', []), participants, p => p.id)
  }),
  options: () => ({ variables: { onlyActive: true } })
});

const withCreateParticipant = withRequest(TicketsService.createParticipant, {
  name: 'createParticipant',
  init: false,
  options: ({
    setAlert, formData: { changedFields }, ticket, refetch
  }) => ({
    variables: { id: ticket.id, ...changedFields },
    onRequestFinished: refetch,
    onRequestError: (err) => {
      if (config.isDevelopment && err) console.error(err);
      setAlert({ alertMsg: 'Erro ao adicionar participante', alertType: 'error', showAlert: true });
    }
  })
});

const withDestroyParticipant = withRequest(TicketsService.destroyParticipant, {
  name: 'destroyParticipant',
  init: false,
  options: ({
    setAlert, refetch
  }) => ({
    onRequestFinished: refetch,
    onRequestError: (err) => {
      if (config.isDevelopment && err) console.error(err);
      setAlert({ alertMsg: 'Erro ao remover participante', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withFormData(() => ({ participantId: '' })),
  withViewerData,
  withMembers,
  withCreateParticipant,
  withDestroyParticipant,
  withProps(({ ticketLoading, createParticipantLoading, destroyParticipantLoading }) => ({
    loading: ticketLoading || createParticipantLoading || destroyParticipantLoading
  })),
  withHandlers({
    createParticipant: ({ createParticipantFetch }) => () => createParticipantFetch(true),
    destroyParticipant: ({ destroyParticipantFetch, ticket }) => (participantId) => {
      const questionText = 'Esse participante  um responsvel do ticket. Deseja excluir como ' +
      'responsvel tambm?';
      const destroyAssignee = includes(map(ticket.assignees, i => i.id), participantId)
        ? window.confirm(questionText)
        : false;

      destroyParticipantFetch(true, '', null, {
        variables: { id: ticket.id, participantId, destroyAssignee }
      });
    }
  })
)(ParticipantsTab);
