import React, { memo } from 'react';
import { get, isEmpty } from 'lodash';
import { compose, withProps, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Box, Grid, Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import Tracker from 'utils/tracker';
import withFormData from 'utils/with-form-data';
import withRequest from 'utils/with-request';
import TicketTagsService from 'services/ticket-tags';

import StandardDialog from 'components/StandardDialog';
import { FormTextField } from 'components/FormFields';
import styles from './styles';

const TicketTagFormModal = ({
  openModal, formData: { validationErrors, title },
  setFormData, onSubmit, loading, onClose, disableNextButton, isNew
}) => (
  <StandardDialog
    onClose={onClose}
    modalTitle={isNew ? 'Nova etiqueta' : 'Editar etiqueta'}
    content={
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12}>
          <FormTextField
            disabled={loading}
            label="Ttulo"
            errors={validationErrors.title}
            value={title || ''}
            onChangeValue={(newVal) => setFormData({ title: newVal })}
          />
        </Grid>
      </Grid>
    }
    actions={
      <React.Fragment>
        <Box
          style={{ width: '100%' }}
          display="flex"
          justifyContent="flex-end"
        >
          <Box display="flex">
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={loading}
            >
              CANCELAR
            </Button>
            <Box ml={2}>
              <Button
                color="primary"
                size="large"
                variant="contained"
                onClick={onSubmit}
                disabled={disableNextButton}
              >
                SALVAR
              </Button>
            </Box>
          </Box>
        </Box>
      </React.Fragment>
    }
    fullWidth
    open={openModal}
  />
);

TicketTagFormModal.propTypes = {
  openModal: PropTypes.bool,
  onClose: PropTypes.func,
  onSubmit: PropTypes.func,
  loading: PropTypes.bool,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  disableNextButton: PropTypes.bool,
  isNew: PropTypes.bool
};

const withCreate = withRequest(TicketTagsService.createTag, {
  name: 'create',
  init: false,
  options: async ({
    formData, setFormData, resetFormData, setAlert, onClose, refetch
  }) => ({
    variables: formData.changedFields,
    onRequestFinished: () => {
      Tracker.hit('TicketTag - Created');
      resetFormData();
      refetch(true);
      setAlert({
        alertMsg: 'Etiqueta criada com sucesso',
        alertType: 'success',
        showAlert: true
      });
      onClose();
    },
    onRequestError: error => {
      setFormData({ validationErrors: get(error, 'errors', {}) });
      setAlert({
        alertMsg: 'Erro ao criar etiqueta',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});
const withUpdate = withRequest(TicketTagsService.updateTag, {
  name: 'update',
  init: false,
  options: async ({
    formData, setFormData, resetFormData, setAlert, onClose, refetch, ticketTag
  }) => {
    return {
      variables: { ...formData.changedFields, id: ticketTag.id },
      onRequestFinished: () => {
        Tracker.hit('TicketTag - Updated');
        resetFormData();
        refetch(true);
        setAlert({
          alertMsg: 'Etiqueta atualizada com sucesso',
          alertType: 'success',
          showAlert: true
        });
        onClose();
      },
      onRequestError: error => {
        setFormData({ validationErrors: get(error, 'errors', {}) });
        setAlert({
          alertMsg: 'Erro ao atualizar etiqueta',
          alertType: 'error',
          showAlert: true
        });
      }
    };
  }
});

export default compose(
  withStyles(styles, { withTheme: true }),
  memo,
  withFormData((props) => {
    if (props.ticketTag) return props.ticketTag;

    return { title: '' };
  }),
  withHandlers({
    onClose: ({ onClose, resetFormData }) => () => {
      resetFormData();
      onClose();
    }
  }),
  withCreate,
  withUpdate,
  withProps(({ formData, createLoading, updateLoading, ticketTag }) => {
    const loading = createLoading || updateLoading;
    return {
      loading,
      disableNextButton: (loading || isEmpty(formData.title)),
      isNew: !ticketTag
    };
  }),
  withHandlers({
    onSubmit: ({ createFetch, updateFetch, ticketTag }) => () => (
      ticketTag ? updateFetch(true) : createFetch(true)
    )
  })
)(TicketTagFormModal);
