/* global window */
import React, { memo } from 'react';
import { compose, withState, withHandlers } from 'recompose';
import PropTypes from 'prop-types';
import moment from 'moment';
import {
  sortBy, get, find, first, uniqBy, map, size, isEmpty, compact, filter, includes
} from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import {
  Grid, Typography, InputAdornment, Select, MenuItem,
  DialogContent, DialogActions, DialogTitle, IconButton, InputLabel,
  FormControl, FormHelperText, TextField, Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import CircularProgress from '@material-ui/core/CircularProgress';
import CreateIcon from '@material-ui/icons/Create';

// Project modules
import {
  FormDateField, FormCurrencyField, FormSelectField, FormTextField
} from 'components/FormFields';
import TicketsService from 'services/tickets';
// Belongs to activities
import withRequest from 'utils/with-request';
import withFormData from 'utils/with-form-data';
import Tracker from 'utils/tracker';
import Session from 'utils/session';
import Screen from 'utils/screen';
import Formatter from 'utils/formatter';
import TicketsEnum from 'enums/tickets';
import { MEMBER_ROLE, AGENT_ROLES } from 'enums/user-roles';
import StandbyButton from './StandbyButton';
import styles from './styles';

const { PRIORITIES, STATUSES } = TicketsEnum;

const MainForm = ({
  setAlert, classes, ticket, setFormData, formData: {
    validationErrors: errors,
    title, priority, deadline, locationId, categoryId, assigneeIds, ticketTagIds,
    status, cost
  }, submitChanges, unarchiveTicketFetch,
  assignables, categories, tags,
  onClose, unarchiveTicketLoading,
  ticketFetch, ticketLoading, theme, isEditingTitle, setIsEditingTitle,
  updateTicketLoading,
  ticketCommentsFetch, deleteTicketLoading,
  deleteTicketFetch, updateTicketFetch,
  moveTicketLoading, archiveTicketFetch, archiveTicketLoading
}) => {
  const isMobile = Screen.isMobile();
  const member = Session.getUserData();
  const locations = sortBy(get(member, 'locations', []), ['name']);
  const memberRole = get(member, 'role', MEMBER_ROLE);
  const isAgentUser = AGENT_ROLES.includes(memberRole);
  let statusValue = get(ticket, 'status');
  const isArchived = Boolean(get(ticket, 'archived_at'));
  const canEditForm = isAgentUser && !isArchived;
  const isFormLoading = Boolean(
    updateTicketLoading || moveTicketLoading || ticketLoading ||
    unarchiveTicketLoading || archiveTicketLoading || deleteTicketLoading
  );
  const isFormDisabled = !canEditForm;
  const isTitleDisabled = Boolean(!isAgentUser || isFormLoading || isFormDisabled);
  const requester = `${get(ticket, 'requester.name', '')} ${get(ticket, 'requester.lastname', '')}`;
  const createdAt = get(ticket, 'created_at') ? moment(ticket.created_at).fromNow() : '';
  const code = get(ticket, 'code', '');
  let priorityValue = get(ticket, 'priority');
  let locationValue = get(ticket, 'location.id', '');
  let categoryValue = get(ticket, 'category.id', '');
  const concatCategories = compact([get(ticket, 'category')].concat(categories || []));
  const allCategories = sortBy(uniqBy(concatCategories, 'id'), 'title');
  const assigneesLabels = map(
    filter(assignables, i => includes(assigneeIds, i.id)), a => `${a.name} ${a.lastname}`
  ).join(', ');
  const tagsValue = map(get(ticket, 'ticket_tags'), t => t.title).join(', ');
  if (isFormDisabled) {
    priorityValue = get(find(PRIORITIES, p => p.key === priorityValue), 'label', '');
    locationValue = get(find(locations, b => b.id === locationValue), 'name', '');
    categoryValue = get(find(categories, c => c.id === categoryValue), 'title', '');
    statusValue = get(find(STATUSES, s => s.key === statusValue), 'label', '');
  }
  const { mainColor } = theme;
  const handleArchive = () => {
    if (canEditForm) {
      if (window.confirm('Tem certeza em arquivar esse ticket?')) {
        archiveTicketFetch(true);
      }
    }
  };
  const handleUnarchive = () => unarchiveTicketFetch(true);
  const handleDelete = () => {
    if (isAgentUser) {
      if (window.confirm('Deseja excluir este ticket? Essa ao  irreversvel.')) {
        deleteTicketFetch(true);
      }
    }
  };
  const handleEditTitle = () => {
    window.document.querySelector('#title').focus();
    setIsEditingTitle(true);
  };
  const onChangeTicketTitle = (e) => {
    // Enter = 13
    e.stopPropagation();
    if (e.keyCode === 13 && !isEmpty(title)) {
      submitChanges();
      setIsEditingTitle(false);
    // Esc = 27
    } else if (e.keyCode === 27) {
      setIsEditingTitle(false);
    }
  };
  return (
    <React.Fragment>
      <DialogTitle>
        <TextField
          multiline
          id="title"
          disabled={isTitleDisabled}
          InputProps={{
            disableUnderline: true,
            style: {
              color: '#000',
              fontSize: 20,
              width: isMobile ? 300 : 350,
              backgroundColor: isEditingTitle ? '#E0E0E0' : '',
              borderRadius: isEditingTitle ? 5 : '',
              paddingLeft: 5
            },
            endAdornment: (
              <InputAdornment position="end">
                {
                  isAgentUser && !isMobile && !isEditingTitle && !isTitleDisabled &&
                    <IconButton
                      onClick={handleEditTitle}
                      disabled={isFormLoading || isTitleDisabled}
                    >
                      {
                        updateTicketLoading ?
                          <CircularProgress
                            color={mainColor}
                            className={classes.progress}
                            variant="indeterminate"
                            disableShrink
                            thickness={5}
                            size={25}
                          />
                          :
                          !isEditingTitle && <CreateIcon fontSize="small" />
                      }
                    </IconButton>
                }
              </InputAdornment>
            )
          }}
          value={title}
          onFocus={handleEditTitle}
          onKeyDown={onChangeTicketTitle}
          onBlur={() => {
            submitChanges();
            setIsEditingTitle(false);
          }}
          onChange={e => setFormData({ title: e.target.value })}
        >
          {errors.title && <FormHelperText>Ttulo no pode ficar em branco</FormHelperText>}
        </TextField>
        <br />
        <Typography variant="body2" color="textSecondary" component="small">
          {ticketLoading ? '' : `#${code} - ${createdAt} por `}
          <b>{requester}</b>
        </Typography>
        <IconButton className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.ticketContent}>
        <FormSelectField
          errors={get(errors, 'status', [])}
          label="Status"
          value={status}
          disabled={isFormLoading || !canEditForm}
          readOnly={isFormDisabled}
          onChangeValue={newVal => setFormData({ status: newVal })}
          onClose={submitChanges}
          options={STATUSES}
          optionValueField="key"
          size="small"
        />
        {
          size(locations) > 1 &&
            <FormSelectField
              errors={get(errors, 'location_id', [])}
              label="Local"
              value={locationId}
              disabled={isFormLoading || !canEditForm}
              readOnly={isFormDisabled}
              onChangeValue={newVal => setFormData({ locationId: newVal })}
              onClose={submitChanges}
              options={locations}
              optionValueField="id"
              optionLabelField="name"
              size="small"
            />
        }
        <FormSelectField
          errors={get(errors, 'category_id', [])}
          label="Categoria"
          value={categoryId}
          disabled={isFormLoading || !canEditForm}
          readOnly={isFormDisabled}
          className={classes.categorySelect}
          onChangeValue={newVal => setFormData({ categoryId: newVal })}
          onClose={submitChanges}
          options={allCategories}
          optionValueField="id"
          optionLabelField="title"
          size="small"
        />
        {
          size(tags) ?
            (isFormDisabled ?
              <FormTextField
                label="Etiquetas"
                value={tagsValue}
                disabled={isFormLoading || isFormDisabled}
                readOnly={isFormDisabled}
                size="small"
              />
              :
              <FormControl
                fullWidth
                margin="dense"
                variant="outlined"
                className={classes.multiSelect}
                error={!!errors.ticket_tags}
              >
                <InputLabel shrink>Etiquetas</InputLabel>
                <Select
                  error={!!errors.ticket_tags}
                  label="Etiquetas"
                  multiple
                  value={ticketTagIds}
                  onChange={e => setFormData({ ticketTagIds: e.target.value })}
                  onClose={submitChanges}
                  disabled={isFormLoading || isFormDisabled}
                  size="small"
                >
                  {tags.map(t => <MenuItem key={t.id} value={t.id}>{t.title}</MenuItem>)}
                </Select>
                {errors.ticket_tags && <FormHelperText>{errors.ticket_tags}</FormHelperText>}
              </FormControl>
            )
            : <React.Fragment />
        }
        {
          isFormDisabled ?
            <FormTextField
              label="Responsveis"
              value={assigneesLabels}
              disabled={isFormLoading || isFormDisabled}
              readOnly={isFormDisabled}
              size="small"
            />
            :
            <FormControl
              fullWidth
              margin="dense"
              variant="outlined"
              className={classes.multiSelect}
              error={!!errors.assignees}
            >
              <InputLabel shrink>Responsveis</InputLabel>
              <Select
                error={!!errors.assignees}
                label="Responsveis"
                multiple
                value={assigneeIds}
                onChange={e => setFormData({ assigneeIds: e.target.value })}
                onClose={submitChanges}
                disabled={isFormLoading || isFormDisabled}
                size="small"
              >
                {assignables.map(l => (
                  <MenuItem key={l.id} value={l.id}>{`${l.name} ${l.lastname}`}</MenuItem>
                ))}
              </Select>
              {errors.assignees && <FormHelperText>{errors.assignees}</FormHelperText>}
            </FormControl>
        }
        <Grid container direction="row" justify="flex-start" spacing={1} className={classes.gridDeadline}>
          <Grid item xs={4}>
            <FormDateField
              errors={get(errors, 'deadline')}
              label="Prazo"
              value={deadline}
              disabled={isFormLoading || isFormDisabled}
              readOnly={isFormDisabled}
              size="small"
              onChangeValue={newVal => {
                setFormData({ deadline: newVal && newVal.format() });
                setTimeout(submitChanges, 10);
              }}
            />
          </Grid>
          <Grid item xs={4}>
            <FormSelectField
              errors={get(errors, 'priority', [])}
              label="Prioridade"
              value={priority}
              disabled={isFormLoading || !canEditForm}
              readOnly={isFormDisabled}
              onChangeValue={newVal => setFormData({ priority: newVal })}
              onClose={submitChanges}
              options={PRIORITIES}
              optionValueField="key"
              size="small"
            />
          </Grid>
          <Grid item xs={4}>
            <FormCurrencyField
              errors={get(errors, 'cost', [])}
              label="Custo"
              value={cost}
              disabled={isFormLoading || !canEditForm}
              readOnly={isFormDisabled}
              size="small"
              onBlurValue={newValue => {
                const params = {
                  variables: {
                    id: ticket.id,
                    cost: Formatter.fromCurrencyToNumber(newValue)
                  }
                };
                updateTicketFetch(true, '', null, params);
              }}
              onChangeValue={newValue => setFormData({ cost: newValue })}
            />
          </Grid>
        </Grid>
      </DialogContent>
      {
        !isArchived && isAgentUser &&
          <DialogActions className={classes.ticketActions}>
            <Button
              color="default"
              size="medium"
              variant="outlined"
              onClick={handleArchive}
              disabled={isFormLoading}
            >
              Arquivar
            </Button>
            <StandbyButton
              setAlert={setAlert}
              ticket={ticket}
              isFormLoading={isFormLoading}
              onSuccess={() => {
                ticketFetch(true);
                ticketCommentsFetch(true);
              }}
            />
          </DialogActions>
      }
      {
        isArchived && isAgentUser &&
          <DialogActions className={classes.ticketActions}>
            <Button
              color="default"
              size="medium"
              variant="outlined"
              onClick={handleUnarchive}
              disabled={isFormLoading}
            >
              DESARQUIVAR
            </Button>
            <Button
              color="secondary"
              size="medium"
              variant="outlined"
              onClick={handleDelete}
              disabled={isFormLoading}
            >
              EXCLUIR TICKET
            </Button>
          </DialogActions>
      }
    </React.Fragment>
  );
};

MainForm.propTypes = {
  setAlert: PropTypes.func,
  classes: PropTypes.object,
  onClose: PropTypes.func,
  ticket: PropTypes.object,
  setFormData: PropTypes.func,
  formData: PropTypes.object,
  submitChanges: PropTypes.func,
  updateTicketLoading: PropTypes.bool,
  ticketCommentsFetch: PropTypes.func,
  ticketFetch: PropTypes.func,
  ticketLoading: PropTypes.bool,
  moveTicketLoading: PropTypes.bool,
  archiveTicketFetch: PropTypes.func,
  archiveTicketLoading: PropTypes.bool,
  unarchiveTicketFetch: PropTypes.func,
  unarchiveTicketLoading: PropTypes.bool,
  tags: PropTypes.array,
  categories: PropTypes.array,
  assignables: PropTypes.array,
  theme: PropTypes.object,
  isEditingTitle: PropTypes.bool,
  setIsEditingTitle: PropTypes.func,
  updateTicketFetch: PropTypes.func,
  deleteTicketFetch: PropTypes.func,
  deleteTicketLoading: PropTypes.bool
};

const withAssignables = withRequest(TicketsService.getAssignables, {
  name: 'assignables',
  props: ({ assignablesData }) => ({ assignables: get(assignablesData, 'members', []) })
});

const withUpdateTicket = withRequest(TicketsService.updateTicket, {
  name: 'updateTicket',
  init: false,
  options: ({
    setAlert, setFormData, formData: { changedFields }, resetFormData, ticket, standardRefetch
  }) => ({
    variables: { id: ticket.id, ...changedFields, standardRefetch },
    onRequestFinished: () => {
      standardRefetch().then(resetFormData);
      setAlert({
        alertMsg: `Ticket #${ticket.code} atualizado com sucesso`,
        alertType: 'success',
        showAlert: true
      });
    },
    onRequestError: error => {
      const errors = get(error, 'errors');
      if (errors) {
        setFormData({ validationErrors: get(error, 'errors', {}) });
      } else {
        const msg = error.error === 'forbidden' || error.response.status === 403 ?
          'Voc no tem permisso suficiente' :
          'Erro ao alterar este ticket';
        setAlert({
          alertMsg: msg, alertType: 'error', showAlert: true
        });
      }
    }
  })
});

const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false,
  options: ({ setAlert, ticket, setFormData, resetFormData, standardRefetch }) => ({
    onRequestFinished: () => {
      standardRefetch().then(resetFormData);
      setAlert({
        alertMsg: `Ticket #${ticket.code} atualizado com sucesso`,
        alertType: 'success',
        showAlert: true
      });
    },
    onRequestError: error => {
      const errors = get(error, 'errors');
      if (errors) {
        setFormData({ validationErrors: get(error, 'errors', {}) });
      } else {
        setAlert({
          alertMsg: 'Erro ao alterar este ticket', alertType: 'error', showAlert: true
        });
      }
    }
  })
});

const withArchiveTicket = withRequest(TicketsService.archiveTicket, {
  name: 'archiveTicket',
  init: false,
  options: ({ ticket, onClose }) => ({
    variables: { ticketsIds: [ticket.id] },
    onRequestFinished: () => {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('Ticket - Archived', trackProps);
      onClose(null, `Ticket #${ticket.code} arquivado com sucesso`);
    },
    onRequestError: err => {
      onClose(err, `Erro ao arquivar ticket #${ticket.code}`);
    }
  })
});

const withUnarchiveTicket = withRequest(TicketsService.unarchiveTicket, {
  name: 'unarchiveTicket',
  init: false,
  options: ({ ticket, onClose }) => ({
    variables: { ticketsIds: [ticket.id] },
    onRequestFinished: () => {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('Ticket - Unarchived', trackProps);
      onClose(null, `Ticket #${ticket.code} desarquivado com sucesso`);
    },
    onRequestError: err => {
      onClose(err, `Erro ao desarquivar ticket #${ticket.code}`);
    }
  })
});

const withDeleteTicket = withRequest(TicketsService.deleteTicket, {
  name: 'deleteTicket',
  init: false,
  options: ({ ticket, onClose }) => ({
    variables: ticket.id,
    onRequestFinished: () => {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('Ticket - Deleted', trackProps);
      onClose(null, `Ticket #${ticket.code} excludo com sucesso`);
    },
    onRequestError: err => {
      onClose(err, `Erro ao excluir ticket #${ticket.code}`);
    }
  })
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withFormData(({ ticket }) => {
    return ({
      title: ticket.title,
      priority: ticket.priority,
      deadline: ticket.deadline,
      cost: ticket.cost,
      locationId: ticket.location.id,
      categoryId: ticket.category.id,
      assigneeIds: map(ticket.assignees, i => i.id),
      ticketTagIds: map(ticket.ticket_tags, i => i.id),
      status: ticket.status
    });
  }),
  withState('isEditingTitle', 'setIsEditingTitle', false),
  withAssignables,
  withHandlers({
    standardRefetch: ({ ticketFetch, ticketCommentsFetch }) => () => {
      ticketCommentsFetch(true);
      return ticketFetch(true);
    }
  }),
  withUpdateTicket,
  withMoveTicket,
  withArchiveTicket,
  withUnarchiveTicket,
  withDeleteTicket,
  withHandlers({
    submitChanges: ({
      formData: { changedFields }, ticket, updateTicketFetch, moveTicketFetch
    }) => () => {
      const changedFieldName = first(Object.keys(changedFields));

      if (includes(['status', 'locationId'], changedFieldName)) {
        const params = { variables: { id: ticket.id, ...changedFields } };
        return moveTicketFetch(true, '', null, params);
      }

      const updateFields = [
        'title', 'priority', 'deadline', 'cost', 'locationId', 'categoryId', 'assigneeIds',
        'ticketTagIds'];
      if (includes(updateFields, changedFieldName)) {
        const params = { variables: { id: ticket.id, ...changedFields } };
        return updateTicketFetch(true, '', null, params);
      }

      return null;
    }
  })
)(MainForm);
