/* global window */
import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import { TableCell, TableRow, Button, IconButton } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import withViewerData from 'utils/with-viewer-data';
import Tracker from 'utils/tracker';
import withRequest from 'utils/with-request';
import TicketTagsService from 'services/ticket-tags';
import TicketTagFormModal from '../TicketTagFormModal';
import styles from './styles';

const EditButtonBase = ({ ticketTag, refetch, openModal, setOpenModal, setAlert }) => {
  const onClick = () => {
    Tracker.hit('TicketTagModal - Opened');
    setOpenModal(true);
  };
  const onClose = async () => {
    Tracker.hit('TicketTagModal - Closed');
    setOpenModal(false);
  };
  return (
    <React.Fragment>
      <Button variant="outlined" size="medium" onClick={onClick}>
        Editar
      </Button>
      <TicketTagFormModal
        ticketTag={ticketTag}
        onClose={onClose}
        setAlert={setAlert}
        refetch={refetch}
        openModal={openModal}
      />
    </React.Fragment>
  );
};

EditButtonBase.propTypes = {
  ticketTag: PropTypes.object,
  openModal: PropTypes.bool,
  setOpenModal: PropTypes.func,
  setAlert: PropTypes.func,
  refetch: PropTypes.func
};

const EditButton = compose(
  withState('openModal', 'setOpenModal', false)
)(EditButtonBase);

const TicketTagsItemRow = memo(({
  viewer, index, classes, setAlert, ticketTag, loading,
  ticketTagDeleteFetch, refetch
}) => {
  const onRemove = async () => {
    if (window.confirm('Deseja realmente excluir essa etiqueta?')) {
      ticketTagDeleteFetch(true);
    }
  };
  if (!viewer) return null;
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{ticketTag.title}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <EditButton
          ticketTag={ticketTag}
          setAlert={setAlert}
          refetch={refetch}
        />
        &nbsp;&nbsp;&nbsp;
        <IconButton disabled={loading} onClick={onRemove}>
          <DeleteIcon />
        </IconButton>
      </TableCell>
    </TableRow>
  );
});

TicketTagsItemRow.propTypes = {
  index: PropTypes.number,
  ticketTag: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  refetch: PropTypes.func,
  loading: PropTypes.bool,
  ticketTagDeleteFetch: PropTypes.func
};

const withTicketTagDelete = withRequest(TicketTagsService.deleteTag, {
  name: 'ticketTagDelete',
  init: false,
  options: ({ ticketTag, setAlert, refetch }) => ({
    variables: ticketTag.id,
    onRequestFinished: () => {
      setAlert({ alertMsg: 'Etiqueta foi excluda', alertType: 'success', showAlert: true });
      refetch(true);
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao excluir etiqueta', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('loading', 'setLoading', false),
  withTicketTagDelete,
  withViewerData
)(TicketTagsItemRow);
