import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { sortBy, get, map, isEmpty } from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography, DialogContent, DialogActions, FormControlLabel, Switch, Button
} from '@material-ui/core';
import PropTypes from 'prop-types';

// Project modules
import Loading from 'components/Loading';
import { FormTextField } from 'components/FormFields';
import TicketCommentsService from 'services/ticket-comments';
import withRequest from 'utils/with-request';
import Tracker from 'utils/tracker';

import TicketComment from '../TicketComment';
import CommentAttachButton from '../CommentAttachButton';
import styles from './styles';

const ActivitiesTab = ({
  classes,
  ticketCommentsFetch, ticketCommentsData, ticketCommentsLoading,
  commentMessage, setCommentMessage,
  sendTicketCommentFetch, sendTicketCommentLoading,
  canEditForm, isArchived, isMemberRequester, archiveTicketLoading, unarchiveTicketLoading,
  setAlert, isAgentUser, ticket
}) => {
  const comments = sortBy(get(ticketCommentsData, 'ticket_comments', []), ['created_at']).reverse();
  const canComment = canEditForm ||
    ticket.viewer_is_only_participant ||
    (isArchived ? false : isMemberRequester);
  const isCommentDisabled = Boolean(
    !canComment || sendTicketCommentLoading || ticketCommentsLoading ||
    archiveTicketLoading || unarchiveTicketLoading
  );
  const lockSendMessageButton = isCommentDisabled || isEmpty(get(commentMessage, 'content', ''));

  const handleSendComment = () => {
    if (canComment) {
      sendTicketCommentFetch(true);
    }
  };

  return (
    <React.Fragment>
      <DialogContent className={classes.commentContent}>
        <div className={classes.commentArea}>
          {
            ticketCommentsLoading ?
              <Loading />
              :
              map(comments, c => (
                <TicketComment
                  key={c.id}
                  comment={c}
                  setAlert={setAlert}
                  fetchComments={ticketCommentsFetch}
                />
              ))
          }
        </div>
        <div className={classes.commentHeaderContainer}>
          <Typography variant="h6">Comentar</Typography>
          {
            isAgentUser &&
              <FormControlLabel
                className={classes.isInternalSwitch}
                control={
                  <Switch
                    checked={commentMessage.isInternal}
                    onChange={() => setCommentMessage({
                      ...commentMessage, isInternal: !commentMessage.isInternal
                    })}
                    color="primary"
                  />
                }
                label="Interno"
                disabled={isCommentDisabled}
              />
          }
        </div>
        <FormTextField
          multiline
          rows={3}
          rowsMax={3}
          placeholder="Escreva seu comentrio aqui..."
          classes={{ root: classes.bgWhite }}
          value={commentMessage.content}
          onChange={e => setCommentMessage({ ...commentMessage, content: e.target.value })}
          disabled={isCommentDisabled}
        />
      </DialogContent>
      <DialogActions className={classes.commentActions}>
        <CommentAttachButton
          size="medium"
          ticketId={get(ticket, 'id')}
          isInternal={commentMessage.isInternal}
          disabled={!!isCommentDisabled}
          onAttachmentSent={() => ticketCommentsFetch(true)}
          onUploadError={(err) => {
            setAlert({
              alertMsg: err.message,
              alertType: 'error',
              showAlert: true
            });
          }}
        />
        <Button
          color="primary"
          size="medium"
          variant="contained"
          onClick={handleSendComment}
          disabled={lockSendMessageButton}
        >
          ENVIAR
        </Button>
      </DialogActions>
    </React.Fragment>
  );
};
ActivitiesTab.propTypes = {
  classes: PropTypes.object,
  ticketCommentsFetch: PropTypes.func,
  ticketCommentsData: PropTypes.object,
  ticketCommentsLoading: PropTypes.bool,
  commentMessage: PropTypes.object,
  setCommentMessage: PropTypes.func,
  sendTicketCommentFetch: PropTypes.func,
  sendTicketCommentLoading: PropTypes.bool,
  canEditForm: PropTypes.bool,
  isArchived: PropTypes.bool,
  isMemberRequester: PropTypes.bool,
  archiveTicketLoading: PropTypes.bool,
  unarchiveTicketLoading: PropTypes.bool,
  setAlert: PropTypes.func,
  isAgentUser: PropTypes.bool,
  ticket: PropTypes.object
};

const withSendTicketComment = withRequest(TicketCommentsService.sendComment, {
  name: 'sendTicketComment',
  init: false,
  options: ({
    ticket, commentMessage,
    setCommentMessage, onClose, ticketCommentsFetch
  }) => ({
    variables: { id: get(ticket, 'id'), ...commentMessage },
    onRequestFinished: () => {
      setCommentMessage(initialCommentState);
      ticketCommentsFetch(false);
    },
    onRequestError: err => {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('TicketModal - Closed', trackProps);
      onClose(err, 'Erro ao enviar um comentrio para esse ticket');
    }
  })
});

const initialCommentState = { content: '', isInternal: false };
export default compose(
  memo,
  withState('commentMessage', 'setCommentMessage', initialCommentState),
  withStyles(styles, { withTheme: true }),
  withSendTicketComment
)(ActivitiesTab);
