import React, { memo } from 'react';
import { compose, withState, withPropsOnChange, lifecycle, withHandlers } from 'recompose';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import { sortBy, get, find, map } from 'lodash';
import { withStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid, Tabs, Tab, Dialog, DialogTitle } from '@material-ui/core';

// Project modules
import TicketsService from 'services/tickets';
// Belongs to activities
import TicketCommentsService from 'services/ticket-comments';
import withRequest from 'utils/with-request';
import Tracker from 'utils/tracker';
import Session from 'utils/session';
import TicketsEnum from 'enums/tickets';
import { MEMBER_ROLE, AGENT_ROLES } from 'enums/user-roles';
import MainForm from './MainForm';
import ActivitiesTab from './ActivitiesTab';
import ParticipantsTab from './ParticipantsTab';
import styles from './styles';

const { PRIORITIES, STATUSES } = TicketsEnum;

const TabPanel = ({ children, activeTabValue, value }) => (
  <div hidden={activeTabValue !== value} id={`scrollable-force-tabpanel-${value}`}>
    { activeTabValue === value && children }
  </div>
);
TabPanel.propTypes = {
  children: PropTypes.any,
  activeTabValue: PropTypes.number,
  value: PropTypes.number
};

const TicketModal = memo(({
  setAlert, classes, categories, tags,
  open, onClose, unarchiveTicketLoading,
  currentTicket,
  ticketFetch, ticketLoading,
  ticketCommentsFetch, ticketCommentsData, ticketCommentsLoading,
  archiveTicketLoading,
  activeTab, setActiveTab
}) => {
  if (!open) return null;
  const member = Session.getUserData();
  const locations = sortBy(get(member, 'locations', []), ['name']);
  const memberRole = get(member, 'role', MEMBER_ROLE);
  const isAgentUser = AGENT_ROLES.includes(memberRole);
  const requesterId = get(currentTicket, 'requester.id');
  const memberId = get(member, 'id');
  let statusValue = get(currentTicket, 'status');
  const isArchived = Boolean(get(currentTicket, 'archived_at'));
  const isMemberRequester = (requesterId && memberId && memberId === requesterId);
  const canEditForm = isAgentUser && !isArchived;
  const isFormDisabled = !canEditForm;
  let priorityValue = get(currentTicket, 'priority');
  let locationValue = get(currentTicket, 'location.id', '');
  let categoryValue = get(currentTicket, 'category.id', '');
  if (isFormDisabled) {
    priorityValue = get(find(PRIORITIES, p => p.key === priorityValue), 'label', '');
    locationValue = get(find(locations, b => b.id === locationValue), 'name', '');
    categoryValue = get(find(categories, c => c.id === categoryValue), 'title', '');
    statusValue = get(find(STATUSES, s => s.key === statusValue), 'label', '');
  }
  const themeForScreen = useTheme();
  const fullScreen = useMediaQuery(themeForScreen.breakpoints.down('sm'));
  const handleClose = () => {
    const trackProps = { id: get(currentTicket, 'id'), title: get(currentTicket, 'title') };
    Tracker.hit('TicketModal - Closed', trackProps);
    onClose();
  };
  return (
    <Dialog
      open={open}
      fullScreen={fullScreen}
      onClose={handleClose}
      keepMounted
      fullWidth
      maxWidth="md"
    >
      <Grid container>
        <Grid item xs={12} sm={12} md={5}>
          <MainForm
            setAlert={setAlert}
            ticket={currentTicket}
            categories={categories}
            ticketFetch={ticketFetch}
            ticketLoading={ticketLoading}
            tags={tags}
            ticketCommentsFetch={ticketCommentsFetch}
            onClose={handleClose}
          />
        </Grid>
        <Grid item xs={12} md={7} className={classes.bgCommentDialog}>
          <Grid item xs={12} md={11} className={classes.bgCommentDialog}>
            <DialogTitle>
              <Tabs
                value={activeTab}
                onChange={(e, newVal) => setActiveTab(newVal)}
                indicatorColor="primary"
              >
                <Tab label="Atividade" />
                <Tab label={`Participantes (${get(currentTicket, 'participants_total_count', 0)})`} />
              </Tabs>
            </DialogTitle>
          </Grid>
          <TabPanel value={0} activeTabValue={activeTab}>
            <ActivitiesTab
              setAlert={setAlert}
              ticket={currentTicket}
              ticketCommentsFetch={ticketCommentsFetch}
              ticketCommentsData={ticketCommentsData}
              ticketCommentsLoading={ticketCommentsLoading}
              canEditForm={canEditForm}
              isArchived={isArchived}
              isMemberRequester={isMemberRequester}
              archiveTicketLoading={archiveTicketLoading}
              unarchiveTicketLoading={unarchiveTicketLoading}
              isAgentUser={isAgentUser}
              onClose={onClose}
            />
          </TabPanel>
          <TabPanel value={1} activeTabValue={activeTab}>
            <ParticipantsTab
              setAlert={setAlert}
              ticket={currentTicket}
              ticketLoading={ticketLoading}
              refetch={() => {
                ticketFetch(true);
              }}
            />
          </TabPanel>
        </Grid>
      </Grid>
    </Dialog>
  );
});

TicketModal.propTypes = {
  setAlert: PropTypes.func,
  open: PropTypes.bool,
  classes: PropTypes.object,
  onClose: PropTypes.func,
  currentTicket: PropTypes.object,
  ticketCommentsFetch: PropTypes.func,
  ticketCommentsData: PropTypes.object,
  ticketCommentsLoading: PropTypes.bool,
  ticketFetch: PropTypes.func,
  ticketLoading: PropTypes.bool,
  archiveTicketLoading: PropTypes.bool,
  unarchiveTicketLoading: PropTypes.bool,
  categories: PropTypes.array,
  tags: PropTypes.array,
  activeTab: PropTypes.number,
  setActiveTab: PropTypes.func
};

const withTicketComments = withRequest(TicketCommentsService.getComments, {
  name: 'ticketComments',
  init: false,
  options: ({ ticket }) => ({ variables: get(ticket, 'id') })
});

const withTicket = withRequest(TicketsService.getTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setCurrentTicket, onClose }) => ({
    variables: get(ticket, 'id'),
    onRequestFinished: data => {
      setCurrentTicket(get(data, 'ticket'));
    },
    onRequestError: err => {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('TicketModal - Closed', trackProps);
      onClose(err, 'Erro ao abrir esse ticket');
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withState('currentTicket', 'setCurrentTicket', {}),
  withState('selectedAssignees', 'setSelectedAssignees', []),
  withState('selectedTags', 'setSelectedTags', []),
  withState('initialTitle', 'setInitialTitle', ''),
  withState('lastTitle', 'setLastTitle', ''),
  withState('activeTab', 'setActiveTab', 0),
  withTicketComments,
  withTicket,
  lifecycle({
    componentDidMount() {
      const {
        ticket, setCurrentTicket, setSelectedAssignees, setSelectedTags
      } = this.props;
      setSelectedAssignees(map(get(ticket, 'assignees'), a => a.id));
      setSelectedTags(map(get(ticket, 'ticket_tags'), t => t.id));
      setCurrentTicket(ticket);
    }
  }),
  withPropsOnChange(['open'], ({
    open, setCurrentTicket, setSelectedAssignees,
    ticketFetch, ticketCommentsFetch, ticket, setInitialTitle, setSelectedTags
  }) => {
    if (open) {
      const trackProps = { id: get(ticket, 'id'), title: get(ticket, 'title') };
      Tracker.hit('TicketModal - Opened', trackProps);
      if (!get(ticket, 'mark_as_read') && !get(ticket, 'viewer_is_only_participant')) {
        TicketsService.markAsRead(ticket.id);
      }
      setCurrentTicket(ticket);
      setSelectedAssignees(map(get(ticket, 'assignees'), a => a.id));
      setSelectedTags(map(get(ticket, 'ticket_tags'), t => t.id));
      setInitialTitle(get(ticket, 'title'));
      ticketFetch(true);
      ticketCommentsFetch(true);
    }
  })
)(TicketModal);
