const Formatter = {
  toCurrency(amount = 0, currency = 'BRL', locale = 'pt-BR', isDecimal = false) {
    const params = {
      style: isDecimal ? 'decimal' : 'currency',
      currency,
      minimumFractionDigits: 2,
      maximumFractionDigits: 2
    };
    return new Intl.NumberFormat(locale, params).format(amount);
  },
  fromCurrencyToNumber(amount) {
    try {
      return Number(amount.replace(/\./g, '').replace(',', '.'));
    } catch {
      return null;
    }
  }
};

export default Formatter;
