import React from 'react';
import { FormControl, FormHelperText } from '@material-ui/core';
import CurrencyTextField from '@unicef/material-ui-currency-textfield';
import { isEmpty, isArray } from 'lodash';
import PropTypes from 'prop-types';

import styles from './styles';

const FormCurrencyField = ({
  errors, variant = 'outlined', InputLabelProps = { shrink: true },
  value, onChangeValue = () => {}, onBlurValue = () => {},
  margin = 'normal',
  ...otherProps
}) => {
  otherProps = variant !== 'standard' ? { ...otherProps, variant } : otherProps;
  return (
    <FormControl fullWidth margin={margin} error={!isEmpty(errors)}>
      <CurrencyTextField
        error={!isEmpty(errors)}
        InputLabelProps={InputLabelProps}
        value={value}
        outputFormat="number"
        currencySymbol=""
        decimalCharacter=","
        digitGroupSeparator="."
        leadingZero="allow"
        onChange={(event, newValue) => onChangeValue(newValue)}
        onBlur={e => onBlurValue(e.target.value)}
        {...otherProps}
      />
      {!isEmpty(errors) &&
        <FormHelperText style={styles.errors}>
          {isArray(errors) ? errors.join(', ') : errors}
        </FormHelperText>}
    </FormControl>
  );
};

FormCurrencyField.propTypes = {
  errors: PropTypes.any,
  variant: PropTypes.string,
  InputLabelProps: PropTypes.shape({ shrink: PropTypes.bool }),
  value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  onChangeValue: PropTypes.func,
  onBlurValue: PropTypes.func,
  margin: PropTypes.string,
  className: PropTypes.any
};

export default FormCurrencyField;
