import FieldNormalizer from 'field-normalizer';

import Api from '../utils/api';

const TicketTags = {
  getTags() {
    return Api.get('/ticket_tags')
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) {
          error.errors = FieldNormalizer.toCamelCase(error.errors);
        }
        throw error;
      });
  },
  createTag({ title }) {
    return Api.post('/ticket_tags', { title })
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) {
          error.errors = FieldNormalizer.toCamelCase(error.errors);
        }
        throw error;
      });
  },
  updateTag({ id, title }) {
    return Api.put(`/ticket_tags/${id}`, { title })
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) {
          error.errors = FieldNormalizer.toCamelCase(error.errors);
        }
        throw error;
      });
  },
  deleteTag(id) {
    return Api.delete(`/ticket_tags/${id}`)
      .then(response => FieldNormalizer.toCamelCase(response))
      .catch(error => {
        if (error.errors) {
          error.errors = FieldNormalizer.toCamelCase(error.errors);
        }
        throw error;
      });
  }
};

export default TicketTags;
