import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import { Typography, Button, useMediaQuery } from '@material-ui/core';
import { withStyles, useTheme } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import Session from '../../../utils/session';
import styles from './styles';

const ServicesWelcome = memo(({ classes, history }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const imgWidth = isSmallScreen ? 275 : 380;
  const imgHeight = isSmallScreen ? 133 : 184;
  return (
    <section className={classes.welcome}>
      <div className={classes.container}>
        <div className={classes.root}>
          <Typography variant="h5" className={classes.center}>
            <b>Solicite servios e conecte-se com nossos parceiros</b>
          </Typography>
          <p className={classes.center}>
            Receba oramentos e contrate o que precisar sem a dor de cabea de
            lidar com vrios fornecedores.
            <br />
            Ns lidamos com eles com carinho e voc fica no controle.
          </p>
          <p className={classes.center}>
            <img src={require('../../../assets/images/services-welcome.png')} width={imgWidth} height={imgHeight} alt="Todos os servios em um nico lugar" />
          </p>
          <br />
          <p className={classes.center}>
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={() => {
                Session.setServicesWelcomeVisited();
                history.replace('/servicos');
              }}
            >
              Comear
            </Button>
          </p>
        </div>
      </div>
    </section>
  );
});

ServicesWelcome.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(ServicesWelcome);
