/* global window */
import React, { memo } from 'react';
import { compose, withHandlers } from 'recompose';
import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import withRequest from 'utils/with-request';
import TicketsService from 'services/tickets';

const StandbyButton = ({
  ticket, isFormLoading,
  loading, onSubmit
}) => {
  const label = ticket.is_standby ? 'Retomar' : 'Deixar em espera';
  const color = ticket.is_standby ? 'primary' : 'default';

  return (
    <Button
      color={color}
      size="medium"
      variant="outlined"
      disabled={isFormLoading || loading}
      onClick={onSubmit}
    >
      { label }
    </Button>
  );
};
StandbyButton.propTypes = {
  ticket: PropTypes.shape({
    is_standby: PropTypes.bool
  }),
  isFormLoading: PropTypes.bool,
  loading: PropTypes.bool,
  onSubmit: PropTypes.func
};

const withToggleStandby = withRequest(TicketsService.toggleStandby, {
  init: false,
  props: ({ data }) => data || {},
  options: ({
    ticket, setAlert, onSuccess
  }) => ({
    variables: ticket.id,
    onRequestFinished: onSuccess,
    onRequestError: err => {
      const alertMsg = err.errors
        ? Object.values(err.errors).flat().join()
        : 'Erro ao comunicar com servidor';
      setAlert({ alertMsg, alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  memo,
  withToggleStandby,
  withHandlers({
    onSubmit: ({ fetch, ticket }) => () => {
      if (!(ticket.is_standby || window.confirm('Tem certeza em deixar esse ticket em espera?'))) {
        return;
      }

      fetch(true);
    }
  })
)(StandbyButton);
