import React, { memo } from 'react';
import { get, size } from 'lodash';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table, TableBody, TableCell, TableHead,
  TableRow, Paper, Button, Typography
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import withRequest from 'utils/with-request';
import withNotAuthorizedHandler from 'utils/with-not-authorized-handler';
import TicketTagsService from 'services/ticket-tags';
import { USER_MANAGER_ROLES } from 'enums/user-roles';
import Loading from 'components/Loading';
import TicketTagsItemRow from '../TicketTagsItemRow';
import TicketTagFormModal from '../TicketTagFormModal';
import styles from './styles';

const COL_SPAN = 2;

const TicketTagsTable = memo(({
  ticketTags, ticketTagsLoading, openTicketTagModal,
  setOpenTicketTagModal, classes, setAlert, ticketTagsFetch
}) => {
  const isLoading = ticketTagsLoading;
  const onOpenNewTicketTag = () => setOpenTicketTagModal(true);
  const onCloseTicketTag = () => setOpenTicketTagModal(false);
  return (
    <React.Fragment>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Etiquetas (${size(ticketTags)})`}</b>
            </Typography>
          </aside>
          <aside>
            <Button
              size="large"
              color="primary"
              variant="contained"
              onClick={onOpenNewTicketTag}
            >
              Nova Etiqueta
            </Button>
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Paper className={classes.tablePaper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableCell}>TTULO</TableCell>
                <TableCell className={classes.tableSmallCell}>AES</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                isLoading ?
                  <TableRow>
                    <TableCell colSpan={COL_SPAN} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    size(ticketTags) ?
                      (ticketTags.map((tTag, i) => {
                        return (
                          <TicketTagsItemRow
                            setAlert={setAlert}
                            index={i}
                            key={tTag.id}
                            ticketTag={tTag}
                            isLoading={isLoading}
                            refetch={ticketTagsFetch}
                          />
                        );
                      }))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={COL_SPAN} align="center">
                          Nenhuma etiqueta cadastrada
                        </TableCell>
                      </TableRow>
                  )
              }
              <TicketTagFormModal
                onClose={onCloseTicketTag}
                openModal={openTicketTagModal}
                setAlert={setAlert}
                refetch={ticketTagsFetch}
              />
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});
TicketTagsTable.propTypes = {
  ticketTagsFetch: PropTypes.func,
  ticketTagsLoading: PropTypes.bool,
  ticketTags: PropTypes.array,
  classes: PropTypes.object,
  openTicketTagModal: PropTypes.bool,
  setOpenTicketTagModal: PropTypes.func,
  setAlert: PropTypes.func
};

const withTicketTags = withRequest(TicketTagsService.getTags, {
  name: 'ticketTags',
  props: ({ ticketTagsData }) => ({ ticketTags: get(ticketTagsData, 'ticketTags') }),
  options: ({ setAlert }) => ({
    variables: false,
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao carregar etiquetas', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('openTicketTagModal', 'setOpenTicketTagModal', false),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withTicketTags
)(TicketTagsTable);
